import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AperturadasComponent } from './aperturadas/aperturadas.component';
import { EnEjecucionComponent } from './en-ejecucion/en-ejecucion.component';
import { LiquidadasComponent } from './liquidadas/liquidadas.component';
import { PendientesComponent } from './pendientes/pendientes.component';
import { TipoProgramaComponent } from './tipo-programa/tipo-programa.component';
import { AvancesAnticiposPagosComponent } from './avances-anticipos-pagos/avances-anticipos-pagos.component';
import { ContratistaComponent } from './contratista/contratista.component';

const routes: Routes = [
  {
    path: '',
    children: [
      { path: 'aperturadas', component: AperturadasComponent },
      { path: 'en-ejecucion', component: EnEjecucionComponent },
      { path: 'liquidadas', component: LiquidadasComponent },
      { path: 'pendientes', component: PendientesComponent },
      { path: 'tipo-programa', component: TipoProgramaComponent },
      { path: 'avances-anticipos-pagos', component: AvancesAnticiposPagosComponent },
      { path: 'contratista', component: ContratistaComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ObrasRoutingModule {}
